import React, { useEffect } from "react";
import { useLocation, withRouter } from "react-router-dom";
import { useReactToPrint } from "react-to-print";
import { supabase } from "../supabaseClient";

function Home() {
  // const [names, setNames] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [confirmPassword, setConfirmPassword] = React.useState("");
  const [success, setSuccess] = React.useState(null);
  const [access_token, setToken] = React.useState(null);
  const [error, setError] = React.useState(null);
  const [loading, setLoading] = React.useState(false);

  // const user = supabase.auth.user();
  // console.log(user);
  const location = useLocation();

  useEffect(() => {
    console.log("ran");
    const access_token = location?.hash
      .split("#")[1]
      ?.split("=")[1]
      .split("&")[0];

    localStorage.setItem("access_token", access_token);
    let token = localStorage.getItem("access_token");
    setToken(token);
    // console.log(token, "token");
  }, []);

  supabase.auth.onAuthStateChange((event, session) => {
    // console.log(event);

    if (event === "PASSWORD_RECOVERY") {
      console.log("PASSWORD_RECOVERY");
      // redirect user to the page where it creates a new password
    } else {
      // save the user session
    }
  });

  const handleResetPassword = async (new_password, confirm_password) => {
    setSuccess(null);
    setError(null);
    setLoading(true);

    if (
      (!new_password && !confirm_password) ||
      !new_password ||
      !confirm_password
    ) {
      setError("Please fill the 'new password and confirm password'");
      setLoading(false);
    } else if (new_password !== confirm_password) {
      setError("Passwords do not match");
      setLoading(false);
    } else {
      try {
        const { error, data } = await supabase.auth.api.updateUser(
          access_token,
          {
            password: new_password,
          }
        );
        if (error) {
          setError(
            "Reset link expired, please request again from the app and try again"
          );
          setLoading(false);
        } else {
          setSuccess("Password reset successful");
        }
      } catch (error) {
        setError(error.message);
      } finally {
        setLoading(false);
      }
    }
  };

  // const signout = async () => {
  //   const { error } = await supabase.auth.signOut();
  //   if (error) {
  //     console.log(error);
  //   }
  // };

  return (
    <div className="App">
      <h1 className="header">Reset your password</h1>
      {error && <p className="error">{error}</p>}
      {success && <p className="success_text">{success}</p>}
      {!success ? (
        <>
          <input
            className="input"
            type="password"
            onChange={(e) => setPassword(e.target.value)}
            placeholder="Enter new password"
          />
          <input
            className="input"
            type="password"
            onChange={(e) => setConfirmPassword(e.target.value)}
            placeholder="Confirm password"
          />
          {loading ? (
            <>
              <h2>Resetting...</h2>
            </>
          ) : (
            <>
              <button
                className="btn"
                onClick={() => handleResetPassword(password, confirmPassword)}
              >
                Reset your Password
              </button>
            </>
          )}
        </>
      ) : (
        <>
          <h2>Return to your app and login with your new password</h2>
        </>
      )}
    </div>
  );
}

export default Home;
